<template>
  <div class="add-rule">
    <el-dialog
      :visible.sync="$parent.ruleVisible"
      width="40%"
      :title="ruleTitle"
      @close="onCancel"
      center>
      <el-form :model="addRuleForm" ref="addRuleForm" :rules="addRuleFormRules" label-width="80px">
        <el-form-item label="灰度的IP" prop="ip">
          <el-select v-model="addRuleForm.ip" placeholder="从实例列表中选择" @change="selectChange">
            <el-option v-for="item in ruleInstanceData"
              :key="item.id"
              :label="item.ip"
              :value="item.ip">
            </el-option>
          </el-select>
          <div class="no-ip">没有你想要的IP？可以<span @click="isEnterIp = !isEnterIp">手动输入IP</span></div>
        </el-form-item>
        <el-form-item class="mb10" v-show="isEnterIp">
          <el-input type="textarea" rows="4" v-model.trim="addRuleForm.ipList" placeholder="输入IP列表，英文逗号隔开，输入完后点击添加按钮"></el-input>
        </el-form-item>
        <el-form-item v-show="isEnterIp">
          <el-button plain size="small" icon="el-icon-plus" @click="addIpList">添加</el-button>
        </el-form-item>
        <el-form-item>
          <div class="ip-list-box">
            <ul class="clearfix">
              <li v-for="(item, index) in paramsIpList" :key="index"><span>{{item}}</span><i class="el-icon-close" @click="deleteIpList(index)"></i></li>
            </ul>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmit">完成</el-button>
          <el-button @click="onCancel">取消</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  data() {
    return {
      addRuleForm: {},
      addRuleFormRules: {},
      paramsIpList: [],
      isEnterIp: false
    }
  },
  props: {
    ruleParams: Object
  },
  computed: {
    ...mapState({
      ruleInstanceData: state => state.config.ruleInstanceData
    }),
    ruleTitle() {
      return this.ruleParams.paramsIpList && this.ruleParams.paramsIpList.length > 0 ? '修改规则' : '新增规则'
    }
  },
  watch: {
    ruleParams: {
      handler(newValue) {
        if (newValue.paramsIpList) {
          this.paramsIpList = newValue.paramsIpList
        }
      },
      deep: true
    }
  },
  methods: {
    ...mapActions('config', ['addGrayRuleData', 'getGrayRuleData']),
    addIpList() {
      const vm = this
      const regx = /(?=(\b|\D))(((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))\.){3}((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))(?=(\b|\D))/
      vm.addRuleForm.ipList.split(',').forEach(item => {
        if (regx.test(item) && !vm.paramsIpList.includes(item)) {
          vm.paramsIpList.push(item)
        } else {
          if (!vm.paramsIpList.includes(item)) {
            vm.$notify({
              title: '警告',
              type: 'warning',
              message: '不合法的IP地址：' + item
            })
          }
        }
      })
    },
    deleteIpList(index) {
      this.paramsIpList.splice(index, 1)
    },
    selectChange(val) {
      const vm = this
      if (vm.paramsIpList.includes(val)) {
        vm.addRuleForm.ip = ''
        return
      }
      vm.paramsIpList.push(val)
      vm.addRuleForm.ip = ''
    },
    onCancel() {
      const vm = this
      vm.paramsIpList = []
      vm.isEnterIp = false
      vm.addRuleForm.ipList = ''
      vm.$parent.ruleVisible = false
    },
    onSubmit() {
      const vm = this
      if (vm.paramsIpList.length === 0) {
        vm.$notify({
          title: '警告',
          type: 'warning',
          message: 'IP列表不能为空'
        })
        return
      }
      vm.addGrayRuleData(vm)
    }
  }
}
</script>

<style lang="scss" scoped>
.no-ip {
  font-size: 12px;
  span {
    color: #409EFF;
    cursor: pointer;
  }
}
.add-rule {
  .el-form-item {
    margin-bottom: 10px;
  }
  .ip-list-box {
    padding: 15px 15px 15px 0;
    border: 1px solid #eee;
    ul li {
      line-height: 34px;
      border: 1px solid #ccc;
      float: left;
      position: relative;
      border-radius: 3px;
      margin-left: 15px;
      margin-bottom: 10px;
      span {
        float: left;
        padding: 0 8px;
      }
      i {
        line-height: 34px;
        float: left;
        padding: 0 8px;
        border-left: 1px solid #ccc;
        cursor: pointer;
        &:hover {
          background-color: #ccc;
        }
      }
    }
  }
}
</style>



