<template>
  <div class="roll-back">
    <el-dialog
      :title="rollBackActive"
      :visible.sync="$parent.backVisible"
      :center="true"
      width="50%">
      <div class="warn-item">
        此操作将会回滚到上一个发布版本，且当前版本作废，但不影响正在修改的配置。可在发布历史页面查看当前生效的版本
        <a href="javascript:void(0);" @click="goToHis">点击查看</a>
      </div>
      <el-table
        v-if="rollBackCompare.length>0"
        :data="rollBackCompare"
        stripe
        style="width: 100%">
        <el-table-column
          label="Type"
          width="180">
          <template slot-scope="scope">
            <span>{{scope.row.type | changeType}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="entity.firstEntity.key"
          label="Key"
          width="180">
        </el-table-column>
        <el-table-column
          prop="entity.firstEntity.value"
          label="回滚前">
        </el-table-column>
        <el-table-column
          prop="entity.secondEntity.value"
          label="回滚后">
        </el-table-column>
      </el-table>
      <div v-else class="no-data">
        配置没有变化
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger"
                   @click="handleRollBack">{{ $t('common.rollback') }}</el-button>
         <el-button size="small" @click="$parent.backVisible = false">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    props: ['rollBackPara'],
    data() {
      return {}
    },
    computed: {
      ...mapState({
        rollBackActive: state => state.config.rollBackActive,
        rollBackCompare: state => state.config.rollBackCompare,
        rollbackConfirm: state => state.config.rollbackConfirm
      })
    },
    methods: {
      ...mapActions('config', ['setRollBackData']),
      handleRollBack() {
        const vm = this
        vm.$parent.backVisible = false
        vm.setRollBackData(vm)
      },
      goToHis() {
        const vm = this
        vm.$parent.backVisible = false
        vm.$router.push({
          name: 'releaseHis',
          query: vm.rollBackPara
        })
      }
    }
  }
</script>

<style type="text/scss" lang="scss" scoped>
  .warn-item {
    padding: 15px 10px;
    background-color: #fcf8e3;
    color: #8a6d3b;
    border: 1px solid #faebcc;
    border-radius: 4px;
    margin-bottom: 20px;
    a {
      color: #337ab7;
      text-decoration: none;
    }
    a:hover {
      text-decoration: underline;
    }
  }

  .no-data {
    padding: 15px 10px;
    font-size: 16px;
    text-align: center;
  }
</style>



