                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button type="text" size="mini" icon="el-icon-edit" @click="openAddRule(namespace.secondEntity, 'edit')">修改</el-button>
                      <el-button type="text" class="btn-text-red" size="mini" icon="el-icon-delete" @click="deleteGrayRule(namespace.secondEntity)">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <div class="gray-config" v-if="namespace.secondEntity.currentGrayView === 'grayInstance'">
                <div class="clearfix config-tit">
                  <div class="pull-right">
                    <el-button type="primary" size="small" icon="el-icon-refresh" @click="getGrayInstanceList(namespace.secondEntity)"></el-button>
                  </div>
                </div>
                <div class="clearfix latest-name mt-10" v-if="namespace.secondEntity.grayInstanceData.list && namespace.secondEntity.grayInstanceData.list.length !== 0">
                  <a @click="goToHis(latestGrayConfigInfo)">{{latestGrayConfigInfo.name}}</a>
                </div>
                <div v-if="namespace.secondEntity.grayInstanceData.list && namespace.secondEntity.grayInstanceData.list.length === 0" class="text-center no-data">无实例信息</div>
                <div v-else>
                  <el-table class="mb-10" :data="namespace.secondEntity.grayInstanceData.list" stripe>
                    <el-table-column prop="appId" label="App ID"></el-table-column>
                    <el-table-column prop="clusterName" label="Cluster Name"></el-table-column>
                    <el-table-column prop="ip" label="IP"></el-table-column>
                    <el-table-column label="配置获取时间">
                      <template slot-scope="scope">
                        {{scope.row.configs[0].releaseDeliveryTime | formatDateTime}}
                      </template>
                    </el-table-column>
                  </el-table>
                  <el-pagination
                    background
                    @current-change="val => grayInstanceHandleCurrentChange(val, namespace.secondEntity)"
                    :current-page.sync="namespace.secondEntity.grayInstanceData.current"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="namespace.secondEntity.grayInstanceData.total">
                  </el-pagination>
                </div>
              </div>
              <div class="change-history" v-if="namespace.secondEntity.currentGrayView === 'grayHistory'">
                <div class="text-center no-data" v-if="namespace.secondEntity.changeHistory.list && namespace.secondEntity.changeHistory.list.length === 0">暂无数据</div>
                <div class="history-list" v-else v-for="(history, index) in namespace.secondEntity.changeHistory.list" :key="index">
                  <div class="clearfix header mt-10 mb-10">
                    <h3 class="pull-left">{{history.dataChangeCreatedBy}}</h3>
                    <div class="pull-right">{{history.dataChangeCreatedTime | formatDateTime}}</div>
                  </div>
                  <table class="el-table el-table--border custom-table" key="gray-history">
                    <thead>
                    <tr>
                      <th class="cell">Type</th>
                      <th class="cell">Key</th>
                      <th class="cell">Old Value</th>
                      <th class="cell">New Value</th>
                      <th class="cell">Comment</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in history.changeSets.createItems" :key="item.id">
                      <td class="cell">新增</td>
                      <td class="cell">{{item.key}}</td>
                      <td class="cell"></td>
                      <td class="cell">{{item.value}}</td>
                      <td class="cell">{{item.comment}}</td>
                    </tr>
                    <tr v-for="item in history.changeSets.updateItems" :key="item.id">
                      <td class="cell">更新</td>
                      <td class="cell">{{item.newItem.key}}</td>
                      <td class="cell">{{item.oldItem.value}}</td>
                      <td class="cell">{{item.newItem.value}}</td>
                      <td class="cell">{{item.newItem.comment}}</td>
                    </tr>
                    <tr v-for="item in history.changeSets.deleteItems" :key="item.id">
                      <td class="cell">删除</td>
                      <td class="cell">{{item.key}}</td>
                      <td class="cell">{{item.value}}</td>
                      <td class="cell"></td>
                      <td class="cell">{{item.comment}}</td>
                    </tr>
                    </tbody>
                  </table>
                </div>
                <div class="text-center mt-20 mb-10" v-if="namespace.secondEntity.changeHistory.totalPage > 1">
                  <el-button size="small" :disabled="namespace.secondEntity.changeHistory.current === namespace.secondEntity.changeHistory.totalPage" @click="loadMore(namespace.secondEntity)">加载更多<i
                    class="el-icon-arrow-down el-icon--right"></i></el-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 创建集群 -->
    <el-dialog
      title="创建集群"
      :visible.sync="clusterVisible"
      width="40%"
      @close="cancelCluster('createCluster')"
      center>
      <el-form :model="clusterForm" :rules="clusterFormRules" ref="createCluster" label-width="80px">
        <div class="cluster-tips">
          <span>Tips：</span>
          <ul>
            <li>通过添加集群，可以使同一份程序在不同的集群（如不同的数据中心）使用不同的配置</li>
            <li>如果不同集群使用一样的配置，则没有必要创建集群</li>
            <li>
              Apollo默认会读取机器上/opt/settings/server.properties(linux)或C:\opt\settings\server.properties(windows)文件中的idc属性作为集群名字，
              如SHAJQ（金桥数据中心）、SHAOY（欧阳数据中心)
            </li>
            <li>在这里创建的集群名字需要和机器上server.properties中的idc属性一致</li>
          </ul>
        </div>
        <el-form-item label="应用AppId" prop="appId">
          {{$route.query.appId}}
        </el-form-item>
        <el-form-item label="集群名称" prop="clusterName">
          <el-input v-model.trim="clusterForm.clusterName" maxlength="15"></el-input>
          <p class="tips-line">(部署集群如:SHAJQ,SHAOY 或自定义集群如:SHAJQ-xx,SHAJQ-yy)</p>
        </el-form-item>
        <el-form-item label="选择环境" prop="checkEnvList">
          <el-checkbox-group v-model="clusterForm.checkEnvList">
            <el-checkbox v-for="(dev, key) in envList" :key="key" :label="dev.env">{{dev.env}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createCluster('createCluster')">{{ $t('common.ok') }}</el-button>
        <el-button @click="cancelCluster('createCluster')">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
    <!-- 创建namespace -->
    <el-dialog
      title="新建Namespace"
      :visible.sync="namespaceVisible"
      width="40%"
      @close="cancelNamespace"
      center>
      <div class="cluster-tips">
        <span>Tips：</span>
        <ul>
          <li>应用可以通过关联公共namespace来覆盖公共Namespace的配置</li>
          <li>如果应用不需要覆盖公共Namespace的配置，那么无需关联公共Namespace</li>
        </ul>
      </div>
      <div class="text-right">
        <el-radio-group v-model="createNamespaceType" size="small">
          <el-radio-button label="link">关联公共Namespace</el-radio-button>
          <el-radio-button label="create">创建Namespace</el-radio-button>
        </el-radio-group>
      </div>
      <el-form :model="namespaceForm" :rules="namespaceFormRules" ref="namespaceForm" key="publicNamespace"
               label-width="100px" v-if="createNamespaceType === 'link'">
        <el-form-item label="AppId" prop="appId">
          {{$route.query.appId}}
        </el-form-item>
        <el-form-item prop="checkedCluster" label="选择集群" class="select-clusters">
          <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">环境<span
            class="right-text">集群</span></el-checkbox>
          <el-checkbox-group v-model="namespaceForm.checkedCluster" @change="handleCheckedClusterChange">
            <el-checkbox v-for="(cluster, index) in clusters" :label="cluster.env + '-' + cluster.cluster" :key="index">{{cluster.env}}<span class="right-text">{{cluster.cluster}}</span></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="namespace" prop="namespacePublic">
          <el-select v-model="namespaceForm.namespacePublic" placeholder="请选择Namespace">
            <el-option v-for="publicItem in publicNamespace" :key="publicItem.id" :label="publicItem.name" :value="publicItem.name"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-form :model="createNamespaceForm" :rules="createNamespaceFormRules" ref="createNamespaceForm"
               key="createNamespace" v-else>
        <el-form-item label="AppId" prop="appId">
          {{$route.query.appId}}
        </el-form-item>
        <el-form-item prop="name" label="名称">
          <el-input v-model.trim="createNamespaceForm.name" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-radio v-model="createNamespaceForm.type" :label="1">public</el-radio>
          <el-radio v-model="createNamespaceForm.type" :label="2">private</el-radio>
        </el-form-item>
        <el-form-item prop="comment" label="备注">
          <el-input v-model.trim="createNamespaceForm.comment" maxlength="50"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitPublicNamespace('namespaceForm')" v-if="createNamespaceType === 'link'">{{ $t('common.ok') }}</el-button>
        <el-button type="primary" @click="submitCreateNamespace('createNamespaceForm')"
                   v-else>{{ $t('common.ok') }}</el-button>
        <el-button @click="cancelNamespace">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
    <!-- 发布 -->
    <el-dialog
      :visible.sync="releaseVisible"
      width="60%"
      @close="releaseVisible = false"
      center>
      <span slot="title">
        发布<span class="dialog-title-tips">(只有发布过的配置才会被客户端获取到，此次发布只会作用于当前环境:DEV)</span>
      </span>
      <el-form :model="releaseForm" :rules="releaseFormRules" ref="releaseForm" label-width="120px">
        <el-form-item label="Changes">
          <div v-if="!releaseData.itemModifiedCnt">配置没有变化</div>
          <el-table :data="releaseData.items.filter(newVal => newVal.isModified === true)" stripe v-else>
            <el-table-column prop="item.key" label="Key">
              <template slot-scope="scope">
                {{scope.row.item.key}}
                <span class="state-tags new" v-if="scope.row.isModified && !scope.row.oldValue">新</span>
                <span class="state-tags modify"
                      v-if="scope.row.isModified && scope.row.oldValue && !scope.row.isDeleted">改</span>
                <span class="state-tags delete" v-if="scope.row.isDeleted">删</span>
              </template>
            </el-table-column>
            <el-table-column prop="oldValue" label="发布的值"></el-table-column>
            <el-table-column prop="newValue" label="未发布的值"></el-table-column>
            <el-table-column prop="item.dataChangeLastModifiedBy" label="修改人"></el-table-column>
            <el-table-column label="修改时间">
              <template slot-scope="scope">
                {{scope.row.item.dataChangeLastModifiedTime | formatDateTime}}
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item prop="releaseTitle" label="Release Name">
          <el-input v-model.trim="releaseForm.releaseTitle"></el-input>
        </el-form-item>
        <el-form-item prop="releaseComment" label="Comment">
          <el-input type="textarea" v-model.trim="releaseForm.releaseComment"
                    placeholder="Add an optional extended description..."></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="releaseSubmit('releaseForm')">{{ $t('common.ok') }}</el-button>
        <el-button @click="releaseVisible = false">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
    <add-config :currentCluster="currentEnv + '-' + currentCluster" :configForm="configForm"
                @cancelConfig="cancelConfig"></add-config>
    <!-- 回滚弹窗 -->
    <roll-back :rollBackPara="rollBackPara" ></roll-back>
    <!-- 灰度添加规则 -->
    <add-rule :ruleParams="ruleParams"></add-rule>
    <!-- 删除namespace弹窗 -->
    <el-dialog
      title="删除NameSpace"
      :visible.sync="delSpaceVisible"
      width="30%"
      @close="delNameSpaceClose"
      center>
      <div class="cluster-tips">
        <span>Tips：</span>
        <ul>
          <li>删除Namespace会使所有环境下的集群的该Namespace全部被删除</li>
        </ul>
      </div>
      <el-form :model="deleteNamespaceForm" :rules="deleteNamespaceFormRules" ref="deleteNamespaceForm" label-width="110px">
        <el-form-item label="Namespace：" prop="selectNamespace">
          <el-select v-model="deleteNamespaceForm.selectNamespace" placeholder="请选择Namespace">
            <el-option v-for="item in namespaceOptions"
              :key="item.firstEntity.baseInfo.id"
              :label="item.firstEntity.baseInfo.namespaceName"
              :value="item.firstEntity.baseInfo.namespaceName">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="danger" size="small" @click="delNameSpace">确 定</el-button>
        <el-button size="small" @click="delNameSpaceClose">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 添加灰度弹窗 -->
    <el-dialog
      title="添加灰度"
      :visible.sync="addGrayVisible"
      width="30%">
      <div style="font-size: 15px;line-height: 1.5">
        通过创建灰度版本，您可以对某些配置做灰度测试<br>
        灰度流程为:<br>
        <ul>
          <li>1.创建灰度版本</li>
          <li>2.配置灰度配置项</li>
          <li>3.配置灰度规则.如果是私有的namespace可以按照客户端的IP进行灰度，如果是公共的namespace则可以同时按AppId和客户端的IP进行灰度</li>
          <li>4.灰度发布</li>
        </ul>
        <span>灰度版本最终有两种结果:<b>全量发布</b>和<b>放弃灰度</b></span><br>
        <span><b>全量发布</b>:灰度的配置合到主版本并发布，所有的客户端都会使用合并后的配置</span><br>
        <span><b>放弃灰度</b>:删除灰度版本，所有的客户端都会使用回主版本的配置</span><br>
        注意事项:<br>
        <ul>
          <li>1.如果灰度版本已经有灰度发布过，那么修改灰度规则后，无需再次灰度发布就立即生效</li>
        </ul>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="danger" size="small" @click="addGrayData">确 定</el-button>
        <el-button size="small" @click="addGrayVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 放弃灰度弹窗 -->
    <el-dialog
      title="放弃灰度"
      :visible.sync="delGrayVisible"
      width="30%">
      <span>删除灰度会丢失灰度的配置，确定要删除吗?</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="danger" size="small" @click="delGrayData">确 定</el-button>
        <el-button size="small" @click="delGrayVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 回滚弹窗 -->
    <gray-release :grayReleaseData="grayReleaseData" ></gray-release>
  </div>
</template>

<script>
  import {mapState, mapActions, mapMutations} from 'vuex'
  import editor from 'vue2-ace-editor'
  import addConfig from './add-configuration'
  import rollBack from './roll-back'
  import addRule from './add-rule'
  import grayRelease from './gray-release'
  import Clipboard from 'clipboard'

  let clipboard = new Clipboard('.copy-text')
  export default {
    data() {
      return {
        appId: '',
        currentEnv: '',
        currentCluster: '',
        clusterIndex: 0,
        configVisible: false, // 配置弹窗
        backVisible: false, // 回滚弹窗
        ruleVisible: false, // 添加规则
        delSpaceVisible: false, // 删除NameSpace
        addGrayVisible: false, // 添加灰度
        delGrayVisible: false, // 放弃灰度
        releaseGrayVisible: false, // 灰度发布
        grayReleaseData: [],
        rollBackPara: {},
        configForm: {},
        clusterVisible: false, // 创建集群弹窗
        clusterForm: {
          checkEnvList: []
        },
        clusterFormRules: {
          clusterName: [
            {required: true, message: '请输入集群名称', trigger: 'blur'}
          ],
          checkEnvList: [
            {type: 'array', required: true, message: '请最少选择一个环境', trigger: 'change'}
          ]
        },
        namespaceVisible: false,
        createNamespaceType: 'link',
        namespaceForm: {
          checkedCluster: [],
          namespacePublic: ''
        },
        namespaceFormRules: {
          checkedCluster: [
            {type: 'array', required: true, message: '请至少选择一个环境', trigger: 'change'}
          ],
          namespacePublic: [
            {required: true, message: '请选择一个公共的Namespace', trigger: 'blur'}
          ]
        },
        deleteNamespaceForm: {
          selectNamespace: ''
        },
        deleteNamespaceFormRules: {
          selectNamespace: [
            {required: true, message: '请选择一个namespace', trigger: 'blur'}
          ]
        },
        createNamespaceForm: {
          type: 1
        },
        createNamespaceFormRules: {
          name: [
            {required: true, message: '请输入名称', trigger: 'blur'}
          ]
        },
        releaseData: {},
        releaseForm: {},
        releaseFormRules: {
          releaseTitle: [
            {required: true, message: '请输入发布名称', trigger: 'blur'}
          ]
        },
        checkAll: false,
        isIndeterminate: false,
        releaseVisible: false,
        viewOptions: [
          {
            key: 'table',
            text: '表格',
            icon: 'fa-table'
          },
          {
            key: 'text',
            text: '文本',
            icon: 'fa-font'
          },
          {
            key: 'history',
            text: '更改历史',
            icon: 'fa-history'
          },
          {
            key: 'instance',
            text: '实例',
            icon: 'fa-code-fork'
          }
        ],
        grayViewOptions: [
          {
            key: 'config',
            text: '配置',
            icon: 'fa-code-fork'
          },
          {
            key: 'grayRule',
            text: '灰度规则',
            icon: 'fa-code-fork'
          },
          {
            key: 'grayHistory',
            text: '更改历史',
            icon: 'fa-code-fork'
          },
          {
            key: 'grayInstance',
            text: '灰度实例列表',
            icon: 'fa-code-fork'
          }
        ],
        currentInstance: '',
        isReadOnly: true,
        textContent: '',
        textContentEdit: '',
        grayRuleData: [],
        copyEditor: null,
        textTypeForm: {},
        namespaceName: '',
        historyPage: {
          page: 1,
          size: 10
        },
        // 最新配置信息
        instanceCurrentPage: 1,
        grayInstanceCurrentPage: 1,
        latestConfigInfo: {},
        latestGrayConfigInfo: {},
        grayBranchName: '',
        ruleParams: {},
        oldCurrentNamespace: {},
        currentViews: '',
        currentVersion: '',
        recordGrayName: '',
        instanceTableTh: ['App ID', 'Cluster Name', 'IP']
      }
    },
    components: {
      editor,
      addConfig,
      rollBack,
      addRule,
      grayRelease
    },
    computed: {
      ...mapState({
        envList: state => state.config.envList,
        clusters: state => state.config.clusters,
        grayData: state => state.config.grayData,
        namespaceData: state => state.config.namespaceData,
        isCompletion: state => state.config.isCompletion,
        publicNamespace: state => state.config.publicNamespace,
        namespaceOptions: state => state.config.namespaceOptions
      })
    },
    mounted() {
      const vm = this
      clipboard.on('success', function (e) {
        vm.$message({
          type: 'success',
          message: '复制成功'
        })
      })
    },
    watch: {
      currentEnv(newVal, oldVal) {
        if (newVal !== oldVal) {
          this.clusterIndex = 0
          if (newVal === this.currentEnv) {
            this.$refs['envUl' + newVal][0].children[0].click()
          }
        }
      }
    },
    async activated() {
      const vm = this
      vm.appId = vm.$route.query.appId
      await vm.getEnvClusterList(vm)
      Object.prototype.toString.call(vm.envList) === '[object Array]' && (vm.currentEnv = vm.envList[0].env)
      vm.currentCluster = vm.envList[0].clusters[0].name
      vm.clusterIndex = 0
      console.log(this.currentEnv + '---' + this.currentCluster)
      vm.getNamespaceData(vm)
      vm.getPublicNamespace(vm)
      vm.getMissingNamespace(vm)
    },
    methods: {
      ...mapMutations('config', ['changeNamepsace']),
      ...mapActions('config', ['getEnvClusterList', 'getRollBackData', 'getNamespaceData', 'deleteConfig', 'modifyText', 'getNamespaceHistory', 'getInstanceData', 'getBackCompared', 'release', 'getNewConfigInfo', 'getGrayRuleData', 'getRuleInstance', 'deleteGrayRuleData', 'deleteNameSpace', 'addGrayListData', 'delGrayListData', 'getPublicNamespace', 'postPublicNamespace', 'createNamespace', 'createNewCluster', 'deleteCluster', 'getMissingNamespace', 'postMissingNamespace', 'getInstanceNumber']),
      // 添加、编辑灰度规则
      openAddRule(namespace, type) {
        const vm = this
        vm.ruleParamsCache = {
          appId: vm.appId,
          currentEnv: vm.currentEnv,
          clusterName: vm.currentCluster,
          branchName: namespace.baseInfo.clusterName,
          namespaceName: namespace.baseInfo.namespaceName,
          isNew: true
        }
        if (type === 'edit') {
          vm.ruleParamsCache.releaseId = namespace.grayRuleData.releaseId
          vm.ruleParamsCache.isNew = false
          vm.ruleParamsCache.paramsIpList = namespace.grayRuleData.ruleItems[0] ? namespace.grayRuleData.ruleItems[0].clientIpList : []
        }
        vm.ruleParams = vm.commonFunc.deepCopy(vm.ruleParamsCache, {})
        vm.getRuleInstance(vm)
        vm.ruleVisible = true
      },
      // 删除集群
      deleteCurrentCluster(env, clusterName) {
        const vm = this
        vm.currentEnv = env
        vm.clusterName = clusterName
        vm.$confirm(`是否确认删除集群"${clusterName}"？`, '删除', {
          confirmButtonText: vm.$t('common.ok'),
          cancelButtonText: vm.$t('common.cancel'),
          type: 'warning',
          center: true
        }).then(() => {
          vm.deleteCluster(vm)
        }).catch(() => {})
      },
      // 补缺namepsace
      completion() {
        this.$confirm('是否补缺缺失的命名空间?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'info',
          center: true
        }).then(() => {
          this.postMissingNamespace(this)
        }).catch(() => {})
      },
      showNamespace(namespace) {
        namespace.firstEntity.isCollapse = !namespace.firstEntity.isCollapse
        // if (namespace.firstEntity.isCollapse) {
        //   this.operaitonNamespace = namespace.firstEntity.baseInfo.namespaceName
        // }
      },
      // 删除灰度规则
      deleteGrayRule(namespace) {
        const vm = this
        vm.ruleParams = {
          appId: vm.appId,
          currentEnv: vm.currentEnv,
          clusterName: vm.currentCluster,
          branchName: namespace.baseInfo.clusterName,
          namespaceName: namespace.baseInfo.namespaceName,
          releaseId: namespace.baseInfo.releaseId
        }
        vm.$confirm('是否删除此灰度规则?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          vm.deleteGrayRuleData(vm)
        }).catch(() => {})
      },
      // 发布
      openRelease(namespace) {
        const vm = this
        let date = vm.$moment(new Date()).format('YYYYMMDDhhmmss')
        vm.releaseData = namespace
        vm.releaseForm.releaseTitle = date + '-release'
        vm.releaseVisible = true
        console.log(vm.releaseData)
      },
      // 灰度实例列表
      async getGrayInstanceList(namespace) {
        const vm = this
        vm.namespaceName = namespace.baseInfo.namespaceName
        vm.grayBranchName = namespace.baseInfo.clusterName
        vm.currentViews = namespace.currentGrayView
        const res = await vm.getNewConfigInfo(vm)
        if (res.data && res.data.length === 0) return
        vm.latestGrayConfigInfo = namespace.latestGrayConfigInfo = res.data[0] || {}
        const response = await vm.getInstanceData(vm)
        namespace.grayInstanceData.list = response.data
        namespace.grayInstanceData.totol = response.totalElements
      },
      versionSwitch(namespace, version) {
        namespace.firstEntity.branchName = version
        if (version === 'master') {
          this.currentViews = 'masterInstance'
          this.recordGrayName = ''
        } else {
          this.currentViews = 'grayInstance'
        }
      },
      coverConfig(config, namespace) {
        const vm = this
        console.log(config)
        console.log(namespace)
        vm.configForm = {
          key: config.item.key,
          value: config.item.value,
          comment: config.item.comment,
          appId: vm.appId,
          namespaceName: namespace.baseInfo.namespaceName,
          checkedCluster: [`${vm.currentEnv}-${vm.currentCluster}`]
        }
        vm.configVisible = true
      },
      // 添加、编辑配置
      addConfig(config, namespace, configGray) {
        const vm = this
        if (config.item && config.item.id) {
          vm.configForm = {
            ...config.item,
            appId: vm.appId,
            namespaceName: namespace.baseInfo && namespace.baseInfo.namespaceName ? namespace.baseInfo.namespaceName : namespace.firstEntity.baseInfo.namespaceName
          }
          if (namespace.firstEntity && namespace.firstEntity.branchName === 'gray') {
            vm.configForm.grayName = namespace.firstEntity.baseInfo.clusterName
            vm.recordGrayName = namespace.firstEntity.baseInfo.namespaceName
          } else {
            vm.recordGrayName = ''
          }
          if (configGray) {
            vm.configForm.configGray = configGray
            vm.configForm.grayName = namespace.secondEntity.baseInfo.clusterName
          }
          vm.configVisible = true
        } else {
          vm.configForm = {
            appId: vm.appId,
            namespaceName: config.baseInfo.namespaceName,
            checkedCluster: [`${vm.currentEnv}-${vm.currentCluster}`]
          }
          if (namespace && namespace.branchName === 'gray') {
            vm.configForm.grayName = config.baseInfo.clusterName
            vm.recordGrayName = config.baseInfo.namespaceName
          } else {
            vm.recordGrayName = ''
          }
          vm.configVisible = true
        }
      },
      // 取消配置
      cancelConfig() {
        const vm = this
        vm.configVisible = false
        vm.getNamespaceData(vm)
      },
      // 删除配置
      delConfig(scope, namespace) {
        const vm = this
        vm.configForm = {
          ...scope.row.item,
          appId: vm.appId,
          namespaceName: namespace.baseInfo && namespace.baseInfo.namespaceName ? namespace.baseInfo.namespaceName : namespace.firstEntity.baseInfo.namespaceName
        }
        if (namespace.firstEntity && namespace.firstEntity.branchName === 'gray') {
          vm.configForm.grayName = namespace.secondEntity.baseInfo.clusterName
        }
        vm.$confirm(`是否确认删除配置<span class="confirm-params">${vm.configForm.key} = ${vm.configForm.value}</span>？`, '删除', {
          confirmButtonText: vm.$t('common.ok'),
          cancelButtonText: vm.$t('common.cancel'),
          type: 'warning',
          center: true,
          dangerouslyUseHTMLString: true
        }).then(() => {
          vm.deleteConfig(vm)
        }).catch(() => {})
      },
      // 创建集群时的全选
      handleCheckAllChange(val) {
        const vm = this
        const clusterArr = []
        vm.clusters.forEach(item => clusterArr.push(item.env + '-' + item.cluster))
        vm.namespaceForm.checkedCluster = val ? clusterArr : []
        vm.isIndeterminate = false
        console.log(vm.namespaceForm.checkedCluster)
      },
      // 回滚
      rollBack(obj) {
        const vm = this
        vm.backVisible = true
        vm.rollBackPara = {
          appId: obj.appId,
          env: vm.currentEnv,
          clusterName: obj.clusterName,
          namespaceName: obj.namespaceName
        }
        vm.getRollBackData(vm)
      },
      handleBackCompared(obj) {
        const vm = this
        vm.comparePara = obj
        vm.getBackCompared(vm)
      },
      handleCheckedClusterChange(value) {
        const vm = this
        let checkCount = value.length
        vm.checkAll = checkCount === vm.clusters.length
        vm.isIndeterminate = checkCount > 0 && checkCount < vm.clusters.length
        vm.namespaceForm.checkedCluster = value
        console.log(vm.namespaceForm.checkedCluster)
      },
      // 发布
      releaseSubmit(formName) {
        const vm = this
        vm.$refs[formName].validate(async valid => {
          if (valid) {
            vm.namespaceName = vm.releaseData.baseInfo.namespaceName
            const res = await vm.release(vm)
            if (res.code === 200) {
              vm.$message({
                type: 'success',
                message: '发布成功'
              })
              vm.releaseVisible = false
              vm.getNamespaceData(vm)
            }
          }
        })
      },
      cancelCluster(formName) {
        const vm = this
        vm.clusterVisible = false
        vm.$refs[formName].resetFields()
      },
      createCluster(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            vm.clusterForm.appId = vm.appId
            vm.createNewCluster(vm)
          }
        })
      },
      cancelNamespace() {
        const vm = this
        vm.$refs['namespaceForm'] && vm.$refs['namespaceForm'].resetFields()
        vm.$refs['createNamespaceForm'] && vm.$refs['createNamespaceForm'].resetFields()
        vm.namespaceVisible = false
        vm.isIndeterminate = false
      },
      submitPublicNamespace(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            vm.postPublicNamespace(vm)
          }
        })
      },
      submitCreateNamespace(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            console.log(this.createNamespaceForm)
            vm.createNamespace(vm)
          }
        })
      },
      async getRuleData(namespace) {
        const vm = this
        vm.appId = namespace.appId
        vm.currentEnv = namespace.currentEnv
        vm.clusterName = namespace.clusterName
        vm.namespaceName = namespace.namespaceName
        vm.branchName = namespace.branchName
        const res = await vm.getGrayRuleData(vm)
        vm.changeNamepsace({
          index: vm.oldIndex,
          data: res.data
        })
      },
      async tabClick(namespace, item) {
        const vm = this
        namespace.firstEntity.currentView = item.key
        if (item.key === 'text') {
          vm.formatConfigText(namespace)
        }
        if (item.key === 'history') {
          vm.historyPage.page = 1
          namespace.firstEntity.changeHistory.current = 1
          vm.namespaceName = namespace.firstEntity.baseInfo.namespaceName
          const res = await vm.getNamespaceHistory(vm)
          namespace.firstEntity.changeHistory.list = res.data
          namespace.firstEntity.changeHistory.totalPage = res.totalPages
        }
        if (item.key === 'instance') {
          vm.instanceCurrentPage = 1
          vm.currentViews = 'masterInstance'
          vm.getInstanceList(namespace.firstEntity)
          vm.namespaceName = namespace.firstEntity.baseInfo.namespaceName
          try {
            const response = await vm.getInstanceNumber(vm)
            const resData = response.data
            namespace.firstEntity.instanceNum = resData.instanceNum
            namespace.firstEntity.newInstanceNum = resData.newInstanceNum
          } catch (err) {
            vm.$message.error((err && err.data && err.data.message) || '获取实例数失败')
          }
        }
      },
      // 获取灰度规则
      async grayTabClick(namespace, item, index) {
        const vm = this
        namespace.secondEntity.currentGrayView = item.key
        switch (item.key) {
          case 'grayRule':
            vm.oldIndex = index
            vm.namespaceName = namespace.secondEntity.baseInfo.namespaceName
            vm.branchName = namespace.secondEntity.baseInfo.clusterName
            const res = await vm.getGrayRuleData(vm)
            namespace.secondEntity.grayRuleData = res.data
            break
          case 'grayInstance':
            vm.grayInstanceCurrentPage = 1
            vm.getGrayInstanceList(namespace.secondEntity)
            break
          case 'grayHistory':
            vm.historyPage.page = 1
            namespace.secondEntity.changeHistory.current = 1
            vm.namespaceName = namespace.secondEntity.baseInfo.namespaceName
            vm.grayBranchName = namespace.secondEntity.baseInfo.clusterName
            vm.currentViews = namespace.secondEntity.currentGrayView
            vm.currentVersion = namespace.firstEntity.branchName
            const response = await vm.getNamespaceHistory(vm)
            namespace.secondEntity.changeHistory.list = response.data
            namespace.secondEntity.changeHistory.totalPage = response.totalPages
            break
          default:
            break
        }
      },
      // 更改历史加载更多
      async loadMore(namespace) {
        const vm = this
        if (namespace.changeHistory.current >= namespace.changeHistory.totalPage) return
        vm.namespaceName = namespace.baseInfo.namespaceName
        vm.currentVersion = namespace.branchName ? namespace.branchName : ''
        vm.historyPage.page = namespace.changeHistory.current = namespace.changeHistory.current + 1
        const res = await vm.getNamespaceHistory(vm)
        if (namespace.changeHistory.current > 1) {
          namespace.changeHistory.list = namespace.changeHistory.list.concat(res.data)
        } else {
          namespace.changeHistory.list = res.data
        }
      },
      // 实例切换回调
      async instanceChange(val, namespace) {
        const vm = this
        vm.currentInstance = val
        vm.instanceCurrentPage = 1
        vm.getInstanceList(namespace)
        console.log(namespace)
        vm.namespaceName = namespace.baseInfo.namespaceName
        try {
          const response = await vm.getInstanceNumber(vm)
          const resData = response.data
          namespace.instanceNum = resData.instanceNum
          namespace.newInstanceNum = resData.newInstanceNum
        } catch (err) {
          vm.$message.error((err && err.data && err.data.message) || '获取实例数失败')
        }
      },
      // 获取实例列表
      async getInstanceList(namespace) {
        const vm = this
        vm.namespaceName = namespace.baseInfo.namespaceName
        vm.currentInstance = namespace.instanceName
        const res = await vm.getNewConfigInfo(vm)
        if (res.data && res.data.length === 0) return
        vm.latestConfigInfo = namespace.latestConfigInfo = res.data[0] || {}
        const resInstance = await vm.getInstanceData(vm)
        resInstance.data.forEach(item => {
          if (item.configs) {
            vm.instanceTableTh[3] = '配置获取时间'
          } else {
            vm.instanceTableTh.splice(3, 1)
          }
        })
        namespace.instanceData.list = resInstance.data
        namespace.instanceData.total = resInstance.totalElements
      },
      // 实例分页
      instanceHandleCurrentChange(val, namespace) {
        this.instanceCurrentPage = val
        namespace.instanceData.current = val
        this.getInstanceList(namespace)
      },
      grayInstanceHandleCurrentChange(val, namespace) {
        this.grayInstanceCurrentPage = val
        namespace.grayInstanceData.current = val
        this.getGrayInstanceList(namespace)
      },
      editorEdit() {
        const vm = this
        vm.textContentEdit = vm.textContent
        vm.isReadOnly = false
      },
      cancelEditor() {
        const vm = this
        vm.isReadOnly = true
      },
      async textSubmit(namespace) {
        const vm = this
        vm.textTypeForm = {
          configText: vm.textContentEdit,
          format: namespace.format,
          namespaceId: namespace.baseInfo.id,
          namespaceName: namespace.baseInfo.namespaceName
        }
        await vm.modifyText(vm)
        namespace.currentView = 'table'
        vm.getNamespaceData(vm)
      },
      formatConfigText(namespace) {
        const vm = this
        let result = ''
        namespace.firstEntity.items.forEach(item => {
          // deleted key
          if (!item.item.dataChangeLastModifiedBy) {
            return
          }
          if (item.item.key) {
            // use string \n to display as new line
            let itemValue = item.item.value.replace(/\n/g, '\\n')
            result +=
              item.item.key + ' = ' + itemValue + '\n'
          } else {
            result += item.item.comment + '\n'
          }
        })
        vm.textContent = result
        vm.textContentEdit = result
      },
      editorInit(editor) {
        console.log(editor)
        this.copyEditor = editor
        editor.setReadOnly(true)
        require('brace/ext/language_tools')
        require('brace/mode/properties')
        require('brace/mode/yaml')
        require('brace/theme/eclipse')
        require('brace/snippets/javascript')
        require('brace/snippets/properties')
      },
      editorInitEdit(editor) {
        editor.setReadOnly(false)
        require('brace/ext/language_tools')
        require('brace/mode/properties')
        require('brace/mode/yaml')
        require('brace/theme/eclipse')
        require('brace/snippets/javascript')
        require('brace/snippets/properties')
      },
      goToHis(obj) {
        const vm = this
        vm.$router.push({
          name: 'releaseHis',
          query: {
            appId: obj.appId,
            env: vm.currentEnv,
            clusterName: obj.clusterName,
            namespaceName: obj.namespaceName
          }
        })
      },
      synConfig(obj) {
        const vm = this
        vm.$router.push({
          name: 'synConfig',
          query: {
            appId: obj.appId,
            env: vm.currentEnv,
            clusterName: obj.clusterName,
            namespaceName: obj.namespaceName
          }
        })
      },
      clusterBtn(cluster, index) {
        const vm = this
        vm.currentCluster = cluster.name
        vm.clusterIndex = index
        console.log(vm.currentEnv, vm.currentCluster)
        vm.getNamespaceData(vm)
      },
      handleFilterData(obj) {
        const idx = obj.baseInfo.id
        this.$set(obj, 'searchKey', '')
        if (!obj.olderData) {
          this.$set(obj, 'olderData', obj.items)
        }
        if (obj.filterSelect === idx) {
          this.$set(obj, 'filterFlag', !obj.filterFlag)
          this.$set(obj, 'items', obj.olderData)
        } else {
          this.$set(obj, 'filterSelect', idx)
          this.$set(obj, 'filterFlag', true)
        }
      },
      handleKeyChange(data) {
        if (data.searchKey) {
          data.items = data.olderData.filter(list => list.item.key.toLowerCase().indexOf(data.searchKey.toLowerCase()) >= 0)
        } else {
          data.items = data.olderData
        }
      },
      handleDelSpace() {
        const vm = this
        vm.delSpaceVisible = true
      },
      delNameSpace() {
        const vm = this
        vm.$refs['deleteNamespaceForm'].validate(valid => {
          if (valid) {
            vm.deleteNameSpace(vm)
          }
        })
      },
      delNameSpaceClose() {
        this.$refs['deleteNamespaceForm'].resetFields()
        this.delSpaceVisible = false
      },
      handleGrayAdd(obj) {
        const vm = this
        vm.addGrayParam = {
          appId: obj.appId,
          env: vm.currentEnv,
          clusterName: obj.clusterName,
          namespaceName: obj.namespaceName
        }
        vm.addGrayVisible = true
      },
      addGrayData() {
        const vm = this
        vm.addGrayListData(vm)
      },
      handleGrayDel(obj) {
        const vm = this
        vm.delGrayParam = {
          appId: obj.firstEntity.baseInfo.appId,
          env: vm.currentEnv,
          clusterName: obj.firstEntity.baseInfo.clusterName,
          namespaceName: obj.firstEntity.baseInfo.namespaceName,
          branchName: obj.secondEntity.baseInfo.clusterName
        }
        vm.delGrayVisible = true
      },
      delGrayData() {
        const vm = this
        vm.delGrayListData(vm)
      },
      handleGrayRelease(namespace, publish) {
        const vm = this
        let _items = []
        const dateStr = this.dateFtt('yyyyMMddhhmmss', new Date())
        vm.grayReleasePara = {
          appId: namespace.firstEntity.baseInfo.appId,
          env: vm.currentEnv,
          clusterName: namespace.firstEntity.baseInfo.clusterName,
          namespaceName: namespace.firstEntity.baseInfo.namespaceName,
          branchName: namespace.secondEntity.baseInfo.clusterName
        }
        if (publish === 0) {
          namespace.secondEntity.branchItems.forEach((item) => {
            if (item.isModified || item.isDeleted) {
              _items.push(item)
            }
          })
          vm.$set(vm.grayReleaseData, 'items', _items)
          vm.grayReleaseData.title = '灰度发布 (灰度发布的配置只会作用于在灰度规则中配置的实例)'
          vm.grayReleaseData.releaseTitle = dateStr + '-gray'
        } else {
          namespace.secondEntity.branchItems.forEach((item) => {
            if (!item.isDeleted) {
              _items.push(item)
            }
          })
          vm.$set(vm.grayReleaseData, 'items', _items)
          vm.grayReleaseData.title = '全量发布 (全量发布的配置会作用于全部的实例)'
          vm.grayReleaseData.releaseTitle = dateStr + '-gray-release-merge-to-master'
        }
        // 记录灰度标签
        if (namespace.firstEntity && namespace.firstEntity.branchName === 'gray') {
          vm.recordGrayName = namespace.firstEntity.baseInfo.namespaceName
        } else {
          vm.recordGrayName = ''
        }
        vm.grayReleaseData.itemModifiedCnt = namespace.secondEntity.itemModifiedCnt
        vm.grayReleaseData.publish = publish
        vm.releaseGrayVisible = true
      }
    }
  }
</script>

