          border: none;
          background: white;
          padding: 10px 12px;
          border-bottom-left-radius: 2px;
          border-bottom-right-radius: 2px;
          i {
            margin-right: 5px;
          }
        }
        h3 {
          padding: 10px 12px;
          background: #f2f4f7;
          font-size: 16px;
          font-weight: normal;
        }
        .menu-list {
          padding: 16px 16px 36px;
        }
        .cluster-menu {
          > li {
            margin-top: 12px;
            cursor: pointer;
            @include line-height(21px);
            padding: 0 8px 0 20px;
            &:hover {
              background-color: #f2f4f7;
            }
            &.active {
              color: #409EFF;
            }
          }
        }
      }
    }
    .tooltip-env {
      margin-left: 5px;
      font-size: 16px;
    }
    .el-tag {
      background-color: rgba(144, 147, 153, .1);
      padding: 0 10px;
      height: 32px;
      line-height: 30px;
      font-size: 12px;
      color: #909399;
      -webkit-border-radius: 4px;
      border-radius: 4px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      border: 1px solid rgba(144, 147, 153, .2);
      white-space: nowrap;
      cursor: pointer;
      &.active {
        background-color: rgba(64, 158, 255, .1);
        border-color: rgba(64, 158, 255, .2);
        color: #409eff;
      }
    }
    .el-tag + .el-tag {
      margin-left: 10px;
    }
  }
</style>



