<template>
  <div>
    <el-dialog
      v-if="grayReleaseData.publish === 0"
      :title="grayReleaseData.title"
      :visible.sync="$parent.releaseGrayVisible"
      :center="true"
      width="57.5%">
      <el-form ref="grayForm" :model="grayForm" label-width="120px">
        <el-form-item label="Changes">
          <el-table :data="grayReleaseData.items" stripe key="gray-table" v-if="grayReleaseData.itemModifiedCnt > 0">
            <el-table-column label="Key">
              <template slot-scope="scope">
                {{scope.row.item.key}}
                <span class="state-tags new" v-if="scope.row.isModified && !scope.row.oldValue">新</span>
                <span class="state-tags modify"
                      v-if="scope.row.isModified && scope.row.oldValue && !scope.row.isDeleted">改</span>
                <span class="state-tags delete" v-if="scope.row.isDeleted">删</span>
              </template>
            </el-table-column>
            <el-table-column prop="masterReleaseValue" label="主版本的值"></el-table-column>
            <el-table-column prop="oldValue" label="灰度主版发布的值"></el-table-column>
            <el-table-column prop="newValue" label="灰度主版未布的值"></el-table-column>
            <el-table-column prop="item.dataChangeLastModifiedBy" label="修改人"></el-table-column>
            <el-table-column label="修改时间" width="160">
              <template slot-scope="scope">
                {{scope.row.item.dataChangeLastModifiedTime | formatDateTime}}
              </template>
            </el-table-column>
          </el-table>
          <div v-else>灰度配置没有变化</div>
        </el-form-item>
        <el-form-item label="Release Name">
          <el-input v-model.trim="grayReleaseData.releaseTitle"></el-input>
        </el-form-item>
        <el-form-item label="Comment">
          <el-input type="textarea" v-model.trim="grayForm.comment"
                    placeholder="Add an optional extended description..."></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger"
                   @click="grayRelease">{{ $t('common.release') }}</el-button>
         <el-button size="small" @click="$parent.releaseGrayVisible = false">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
    <el-dialog
      v-if="grayReleaseData.publish === 1"
      :title="grayReleaseData.title"
      :visible.sync="$parent.releaseGrayVisible"
      :center="true"
      width="50%">
      <div v-if="step === 0">
        <div class="tip">全量发布将会把灰度版本的配置合并到主分支，并发布。</div>
        <div class="tip">全量发布后, 您希望</div>
        <div class="tip">
          <el-radio v-model="radio" label="1">删除灰度版本</el-radio>
          <el-radio v-model="radio" label="2">保留灰度版本</el-radio>
        </div>
      </div>
      <span slot="footer" class="dialog-footer" v-if="step === 0">
        <el-button size="small" type="danger"
                   @click="stepNext">{{ $t('common.ok') }}</el-button>
         <el-button size="small"
                    @click="$parent.releaseGrayVisible = false,step = 0">{{ $t('common.cancel') }}</el-button>
      </span>
      <el-form ref="grayForm" :model="grayForm" label-width="120px" v-if="step===1">
        <el-form-item label="Changes">
          <el-table :data="grayReleaseData.items" stripe key="gray-table" v-if="grayReleaseData.itemModifiedCnt > 0">
            <el-table-column label="Key">
              <template slot-scope="scope">
                {{scope.row.item.key}}
                <span class="state-tags new" v-if="scope.row.isModified && !scope.row.oldValue">新</span>
                <span class="state-tags modify"
                      v-if="scope.row.isModified && scope.row.oldValue && !scope.row.isDeleted">改</span>
                <span class="state-tags delete" v-if="scope.row.isDeleted">删</span>
              </template>
            </el-table-column>
            <el-table-column prop="oldValue" label="灰度主版发布的值"></el-table-column>
            <el-table-column prop="newValue" label="灰度主版未布的值"></el-table-column>
            <el-table-column prop="item.dataChangeLastModifiedBy" label="修改人"></el-table-column>
            <el-table-column label="修改时间" width="160">
              <template slot-scope="scope">
                {{scope.row.item.dataChangeLastModifiedTime | formatDateTime}}
              </template>
            </el-table-column>
          </el-table>
          <div v-else>灰度配置没有变化</div>
        </el-form-item>
        <el-form-item label="Release Name">
          <el-input v-model.trim="grayReleaseData.releaseTitle"></el-input>
        </el-form-item>
        <el-form-item label="Comment">
          <el-input type="textarea" v-model.trim="grayForm.comment"
                    placeholder="Add an optional extended description..."></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer" v-if="step === 1">
        <el-button size="small" type="danger"
                   @click="grayRelease">{{ $t('common.release') }}</el-button>
         <el-button size="small"
                    @click="$parent.releaseGrayVisible = false,step = 0">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapActions} from 'vuex'

  export default {
    props: ['grayReleaseData'],
    data() {
      return {
        step: 0,
        radio: '1',
        grayForm: {
          comment: ''
        }
      }
    },
    methods: {
      ...mapActions('config', ['branchPublish', 'mergeAndPublish']),
      grayRelease() {
        const vm = this
        vm.grayReleaseForm = {
          'isEmergencyPublish': false,	// 是否紧急发布
          'releaseComment': vm.grayForm.comment,
          'releaseTitle': vm.grayReleaseData.releaseTitle
        }
        if (vm.grayReleaseData.publish === 0) {
          vm.branchPublish(vm)
        } else {
          if (vm.radio === '1') {
            vm.deleteBranch = true
          } else {
            vm.deleteBranch = false
          }
          vm.mergeAndPublish(vm)
        }
        vm.$parent.releaseGrayVisible = false
      },
      stepNext() {
        this.step = 1
      }
    }
  }
</script>

<style type="text/scss" lang="scss" scoped>
  .tip {
    padding: 10px;
  }
</style>



