<template>
  <div>
    <!-- 新增配置、修改配置-->
    <el-dialog
      :visible.sync="$parent.configVisible"
      width="30%"
      :title="configTitle"
      @close="cancel('configForm')"
      center>
      <el-form :model="configForm" :rules="configFormRules" ref="configForm" label-width="80px">
        <el-form-item label="Key" prop="key">
          <el-input v-model.trim="configForm.key" maxlength="50" :disabled="configForm.id"></el-input>
        </el-form-item>
        <el-form-item label="Value" prop="value">
          <el-input v-model.trim="configForm.value"></el-input>
        </el-form-item>
        <el-form-item label="Comment" prop="comment">
          <el-input type="textarea" v-model.trim="configForm.comment"></el-input>
        </el-form-item>
        <el-form-item label="选择集群" class="select-clusters" v-if="!configForm.id && !configForm.grayName">
          <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">环境<span class="right-text">集群</span></el-checkbox>
          <el-checkbox-group v-model="configForm.checkedCluster" @change="handleCheckedClusterChange">
            <el-checkbox v-for="(cluster, index) in clusters" :label="cluster.env + '-' + cluster.cluster" :key="index">{{cluster.env}}<span class="right-text">{{cluster.cluster}}</span></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="configSubmit('configForm')">{{ $t('common.ok') }}</el-button>
        <el-button @click="cancel('configForm')">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        checkAll: false,
        isIndeterminate: true,
        paramsForm: {},
        configFormRules: {
          key: [
            { required: true, message: '请输入key', trigger: 'blur' }
          ],
          value: [
            { required: true, message: '请输入value', trigger: 'blur' }
          ]
        }
      }
    },
    computed: {
      ...mapState({
        clusters: state => state.config.clusters
      }),
      configTitle() {
        return this.configForm.id ? '修改配置项' : '添加配置项'
      }
    },
    props: {
      configForm: Object,
      currentCluster: String
    },
    methods: {
      ...mapActions('config', ['addConfiguration', 'configGrayPost']),
      configSubmit(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            if (vm.configForm.checkedCluster && vm.configForm.checkedCluster.length === 0) {
              vm.$message({
                type: 'error',
                message: '请至少选择一个集群'
              })
              return
            }
            vm.paramsForm = {
              key: vm.configForm.key,
              value: vm.configForm.value,
              comment: vm.configForm.comment || ''
            }
            if (vm.configForm.configGray) {
              vm.configGrayPost(vm)
            } else {
              vm.addConfiguration(vm)
            }
          }
        })
      },
      cancel(formName) {
        const vm = this
        vm.$refs[formName].resetFields()
        vm.$parent.configVisible = false
      },
      handleCheckAllChange(val) {
        const vm = this
        const clusterArr = []
        vm.clusters.forEach(item => clusterArr.push(item.env + '-' + item.cluster))
        vm.configForm.checkedCluster = val ? clusterArr : []
        vm.isIndeterminate = false
      },
      handleCheckedClusterChange(value) {
        const vm = this
        let checkCount = value.length
        vm.checkAll = checkCount === vm.clusters.length
        vm.isIndeterminate = checkCount > 0 && checkCount < vm.clusters.length
        vm.configForm.checkedCluster = value
      }
    }
  }
</script>



